// paralax
VanillaTilt.init(document.querySelector(".hero__gamepad"), {
  max: 8,
  speed: 200,
  perspective: 1000
});


// swiper
const swiper = new Swiper('.swiper-container', {
  // Optional parameters
  direction: 'horizontal',
  slidesPerView: 4,
  loopedSlides: 1000,

  // Navigation arrows
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  }
});


// accordion
$( function() {
  $( "#accordion" ).accordion();
} );


// nav
const list = document.querySelectorAll('.nav_link');

for (let i=0; i<list.length; i++) {
  list[i].onclick = function() {
    let j = 0;

    while(j < list.length) {
      list[j++].className = 'nav_link';
    }

    list[i].className = 'nav_link is_active';
  }

};


// popup call
function toggle() {
  const popup = document.getElementById('popup');
  popup.classList.toggle('is_active')
}


// next popup
function none() {
  const popupCard = document.getElementById('popupCard');
  const popupThx = document.getElementById('popupThx');
  popupCard.classList.add('not_visible')
  popupThx.classList.remove('not_visible')
}

function noneThx() {
  popup.classList.remove('is_active')
  popupThx.classList.add('not_visible')

  setTimeout( () => {
    popupCard.classList.remove('not_visible')
  }, 1000)
}